import { Grid, Typography } from "@mui/material";
import ProjectCard from "./ProjectCard";
import { Masonry } from "@mui/lab";

export default function Projects() {
    return (
        <Grid container gap={1} direction="column">
            <Typography variant="h5"></Typography>
            <Masonry container spacing={1} columns={{ md: 2, xs: 1 }}>
                <ProjectCard
                    name="yastation"
                    description=" CLI-       ."
                    links={[
                        { name: "    habr", href: "https://habr.com/ru/articles/657657/" },
                        { name: "  github", href: "https://github.com/beldmian/yastation" }
                    ]}
                    tags={["Python", "API"]}
                />
                <ProjectCard
                    name="colorblinder"
                    description="  mpeg-dash   VoD      ."
                    links={[
                        { name: "    habr", href: "https://habr.com/ru/articles/757240/" },
                        { name: "  github", href: "https://github.com/beldmian/colorblinder" }
                    ]}
                    tags={["Golang", "Next.js", "API", "Highload", "Accessibility", " "]}
                />
                <ProjectCard
                    name="invest_dashboard"
                    description="    "
                    links={[
                        { name: "  github", href: "https://github.com/beldmian/invest_dashboard" }
                    ]}
                    tags={["Golang", "Prometheus", "Grafana", "API"]}
                />
                <ProjectCard
                    name="light"
                    description=" event-  golang"
                    links={[
                        { name: "  github", href: "https://github.com/beldmian/light" }
                    ]}
                    tags={["Golang"]}
                />
                <ProjectCard
                    name="avanpost-fingers"
                    description="   "
                    links={[
                        { name: "  github", href: "https://github.com/beldmian/avanpost-fingers" }
                    ]}
                    tags={["Golang", "API", "Highload", "C"]}
                />
                <ProjectCard
                    name="esgrs"
                    description="     ESG- "
                    links={[
                        { name: "  github", href: "https://github.com/beldmian/esgrs" }
                    ]}
                    tags={["Golang", "API", "Svelte"]}
                />
            </Masonry>
        </Grid>
    )
}