import { Box, Chip, Divider, Grid, Stack, Typography } from "@mui/material";

export default function About() {
    return (
        <Grid container direction={"column"} gap={1}>
            <Typography variant="h5"> </Typography>
            <Typography variant="body1" align="justify">  ,  ,       .      Go,     Python, C  Java.  IT    ,      .</Typography>
            <Typography variant="h5"> </Typography>
            <Stack direction={"row"} spacing={1} useFlexGap flexWrap="wrap">
                <Chip variant="filled" label="Go" color="primary"/>
                <Chip variant="filled" label="Python" color="primary"/>
                <Chip variant="outlined" label="HTTP"/>
                <Chip variant="outlined" label="Linux"/>
                <Chip variant="outlined" label="Git"/>
                <Chip variant="outlined" label="SQL"/>
                <Chip variant="outlined" label="PostgreSQL"/>
                <Chip variant="outlined" label="REST"/>
                <Chip variant="outlined" label="Docker"/>
                <Chip variant="outlined" label="Kubernetes"/>
                <Chip variant="outlined" label="  (B2)" color="success"/>
            </Stack>
        </Grid>
    )
}